import React, { useEffect, useState } from "react";
import axios from "axios";
import "./razo.css";
function RecentTransactions() {
  const [transactions, setTransactions] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchTransactions();
  }, []);

  const fetchTransactions = () => {
    const apiUrl = "https://api.razorpay.com/v1/payments?count=20";
    const corsAnywhereUrl = "https://corsproxy.io/?";
    const apiKey =
      "cnpwX3Rlc3RfTjJPdThMSFlOQllYQlA6d3l1aWRKQ090Zk1IT1hWQWg0YndPM0ZM";

    axios
      .get(corsAnywhereUrl + apiUrl, {
        headers: {
          Authorization: `Basic ${apiKey}`,
        },
      })
      .then((response) => {
        setTransactions(response.data.items);
        setIsLoading(false);
      })
      .catch((error) => {
        setError("Error fetching transactions.");
        setIsLoading(false);
        console.log("Error fetching transactions:", error);
      });
  };

  if (isLoading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return <div>{error}</div>;
  }

  return (
    <div>
      <h2>Recent Transactions</h2>
      {transactions.length === 0 ? (
        <div>No transactions found.</div>
      ) : (
        <div>
          <table style={{ tableLayout: "fixed", width: "100%" }}>
            <thead>
              <tr>
                <th>Name</th>
                <th className="amount-table">Amount</th>
              </tr>
            </thead>
          </table>
          {transactions.map((transaction) => (
            <div key={transaction.id}>
              {/* Name: {transaction.notes.name}, Amount: {transaction.amount / 100} INR */}
              <table style={{ tableLayout: "fixed", width: "100%" }}>
                <tbody>
                  <tr>
                    <td>{transaction.notes.name}</td>
                    <td className="amount-table">{transaction.amount}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

export default RecentTransactions;
